[data-anim-fadein] {
	opacity: 0;
}

.paralax-image {
	>* {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	img {
		object-fit: cover;
		width: 100%;
		height: 100%;
	}
}

// [data-animation-fadeY] {
// 	opacity: 0;
// 	transform: translateY(50px);
// }

body {
	&:not(.is-touch) {
		.paralax-image_img[data-scroll] {
			will-change: transform;
			transform: translateY(-30%);
		}

		.paralax-image_img[data-scroll].is-inview {
			transform: translateY(30%);
		}

		.paralax-image {
			>* {
				transform: scale(1.3);
			}
		}
	}
}

