	&_link {
		// height: 100%;
		transform: skewX(10deg);
		// overflow: hidden;
		position: relative;
		cursor: auto;

		&:after {
			content: '';
			position: absolute;
			z-index: 1;
			top: 0;
			bottom: 0;
			right: 0;
			width: 4px;
			background: #bb8643;
			transition: opacity 0.3s ease;
		}

		@include mobile {
			// transform: skewX(0deg);
			transform: skew(0deg, 6deg);
			height: 100%;
			margin-top: 0%;
			height: calc(100% + 30px);

			.v-menu_item:first-child & {
				margin-top: -30px;
			}

			&:after {
				top: 0;
				bottom: auto;
				left: 0;
				width: auto;
				height: 4px;
			}
		}
	}

	&_img {
		transform: skewX(-10deg);

		// height: 100%;
		// // width: 100%;
		// width: 140%;
		// margin-left: -20%;
		@include mobile {
			transform: skew(0deg, -6deg);
			height: 130%;
			margin-top: -15%;

			@media (max-height: 560px) {
				height: 160%;
				margin-top: -20%;
			}

			@media (min-width: 500px) {
				height: 160%;
				margin-top: -20%;
			}
		}

		&.-wrapp-position-sm {

			img,
			video,
			svg {
				object-position: 10% center;

				@media (min-height: 1000px) {
					object-position: 50% center;
				}

				@media (max-width: 1300px) {
					object-position: 30% center;

					@media (min-height: 1000px) {
						object-position: 50% center;
					}
				}

				@include mobile {
					object-position: 50% top;

					@media (min-height: 700px) {
						object-position: 30% top;
					}
				}
			}
		}

		&.-wrapp-position {

			img,
			video,
			svg {
				object-position: 90% center;

				@media (min-height: 1000px) {
					object-position: 70% center;
				}

				@media (max-width: 1300px) {
					object-position: 80% center;

					@media (min-height: 1000px) {
						object-position: 70% center;
					}
				}

				@include mobile {
					object-position: 90% center;

					@media (min-height: 700px) {
						object-position: 70% top;
					}
				}
			}
		}

		// @include mobile {
