	&_name {
		text-shadow: 0px 2px 8px rgba(16, 23, 74, 0.41);
		font-family: $font2;
		font-size: 36px;
		font-style: italic;
		font-weight: 400;
		line-height: 120%;

		/* 43.2px */
		@include mobile {
			font-size: 24px;
		}
	}

	&_description {
		text-shadow: 0px 2px 8px rgba(16, 23, 74, 0.41);
		font-size: 20px;
		font-weight: 400;
		line-height: 140%;
		/* 28px */
		letter-spacing: 0.4px;

		@include mobile {
			font-size: 14px;
			letter-spacing: 0.28px;
		}
	}

	&_info {
		flex-shrink: 0;

		@media (max-width: 1180px) {
			width: 38%;
		}

		@include mobile {
			width: 100%;
		}

		&-icon {
			background: url(../img/icon/icon-switch.png) no-repeat center center / contain;

			@include mobile {
				background: url(../img/icon/icon-hand.svg) no-repeat center center / contain;
			}
		}

		&-items {
			// position: absolute;
			// z-index: 2;
			// pointer-events: none;
			// top: 0;
			// left: 0;
			// bottom: 0;
			// right: 0;
			// display: flex;
			// justify-content: space-between;
			// align-items: flex-end;
			// flex-wrap: wrap;
			// grid-gap: 50px;
			padding: fzd(130px) fzd(150px);

			@media (min-width: 1440px) {
				padding: 130px 150px;
			}

			@media (max-width: 1180px) {
				padding: 100px 30px;
			}

			@include mobile {
				// flex-direction: column;
				padding: 30px 20px;
			}
		}

		&-block {
			border-left: 3px solid #bb8643;
			// padding-left: 15px;
		}

		&-text {
			// position: absolute;
			// z-index: 3;
			// pointer-events: none;
			// top: 50%;
			// left: 50%;
			transform: translate(-50%, -50%);

