//include third-party fonts

@mixin font-face($font-name, $file-name, $font-weight, $font-style) {
	@font-face {
		font-family: quote($font-name);
		font-style: $font-style;
		font-weight: $font-weight;
		src: url('../fonts/' + $file-name + '.woff2') format('woff2'),
			url('../fonts/' + $file-name + '.woff') format('woff');
		font-display: swap;
	}
}

// @include font-face(FontName, FontWeight-FileName, 400, normal);

@include font-face(BethanyElingston, BethanyElingston, 400, normal);
@include font-face(BethanyElingston, BethanyElingstonItalic, 400, italic);

@include font-face(ProssimoText, ProssimoTextLight, 300, normal);
@include font-face(ProssimoText, ProssimoTextLightItalic, 300, italic);
@include font-face(ProssimoText, ProssimoTextRegular, 400, normal);
@include font-face(ProssimoText, ProssimoTextRegularItalic, 400, italic);
@include font-face(ProssimoText, ProssimoTextMedium, 500, normal);
@include font-face(ProssimoText, ProssimoTextMediumItalic, 500, italic);
