// breakpoints (desktop first)
$b-mobile-mini: 479px;
$b-mobile-midi: 639px;
$b-mobile: 699px;
$b-tablet: 899px;
$b-tablet-maxi: 1023px;
$b-labtop-mini: 1299px;
$b-laptop: 1439px;
$b-desktop: 1899px;
$b-desktop-maxi: 2239px;

//fonts
$font1Name: 'ProssimoText';
$font2Name: 'BethanyElingston';
$font1: $font1Name, helvetica, tahoma, arial, sans-serif;
$font2: $font2Name, helvetica, tahoma, arial, sans-serif;

// $font1r: 'ProssimoTextRegular', helvetica, tahoma, arial, sans-serif;
// $font1li: 'ProssimoTextLightItalic', helvetica, tahoma, arial, sans-serif;
// $font1m: 'ProssimoTextMedium', helvetica, tahoma, arial, sans-serif;
// $font12li: 'ProssimoLightItalic', helvetica, tahoma, arial, sans-serif;
// $font12eli: 'ProssimoExtraLightItalic', helvetica, tahoma, arial, sans-serif;
// $font12l: 'ProssimoTextLight', helvetica, tahoma, arial, sans-serif;
// $font2r: 'BethanyElingstonRegular', helvetica, tahoma, arial, sans-serif;
// $font2i: 'BethanyElingstonItalic', helvetica, tahoma, arial, sans-serif;

//colors
$white: #fff;
$c-black: #000;
$c-white: #fff;
$c-accent: #fff;
$gray-900: #1d1e23;

$sia-blue: #11174a;
$gold: #a1661c;

// timing
$time-sm: 300ms;
$time-md: 500ms;
$time-lg: 800ms;
$time-xl: 1100ms;

// layout
$container: 1180px;
$wrap-lg: 60px;
$wrap-md: 45px;
$wrap-sm: 30px;

// tags
$h: 'h1, h2, h3, h4, h5, h6';
$input-txt: 'input[type="email"], input[type="number"], input[type="password"], input[type="search"], input[type="tel"], input[type="text"], input[type="url"], input[type="color"], input[type="date"], input[type="datetime"], input[type="datetime-local"], input[type="month"], input[type="time"], input[type="week"], textarea';

// z-index
$z-index: (
	cursor: 100000000,
	modal: 1200,
	overlay: 1100,
	header: 1000,
	footer: 900,
	dropdown: 800,
);

// Base transition function
$transition-duration-base: 0.35s;
$transition-easing-base: cubic-bezier(0.3, 0.86, 0.36, 0.95);

$s-phone: 375px;
$tablet: 700px;

// fonts size

$vw-base: 14.4px;
$vw-base-laptop: 14.4px;
$vw-base-mobile: 3.75px;

@function fzd($size) {
	$vw-size: $size / $vw-base;
	@return $vw-size * 1vw;
}

@function fzl($size) {
	$vw-size: $size / $vw-base-laptop;
	@return $vw-size * 1vw;
}

@function fzm($size) {
	$vw-size: $size / $vw-base-mobile;
	@return $vw-size * 1vw;
}

