.s-hero {
	height: 100vh;
	height: calc(var(--vh, 1vh) * 100);
	background: rgb(191, 218, 239);
	z-index: 1000;

	&_inner {}

	picture {}

	&_bg {

		img,
		svg,
		video {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&-cloud {

			img,
			svg,
			video {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: center top;
			}
		}

		&-right-top {}

		&-left-bottom {}
	}

	&_time-video {
		font-size: 16px;
		font-weight: 300;
		line-height: 110%;
		letter-spacing: 0.32px;

		@media (min-width: 1024px) and (max-height: 745px) {
			bottom: 28px;
		}
	}

	&_content {
		@media (max-height: 835px) and (min-width: 1024px) {
			padding-bottom: 30px;
			padding-top: 20px;
		}
	}

	&_logo {
		@media (min-width: 1024px) and (max-height: 900px) {
			margin-bottom: 5vh;
		}

		@media (min-width: 1024px) and (max-height: 810px) {
			margin-bottom: 20px;
		}
	}

	&_tt {

		.h1,
		h1 {
			font-family: $font2;
			color: var(--dark-blue, #11174a);
			font-size: 100px;
			font-style: normal;
			font-weight: 400;
			line-height: 1.2;

			@include tabletMaxi {
				font-size: 50px;
			}

			@include mobile {
				font-size: 40px;
			}
		}

		span {
			font-style: normal;
			font-weight: normal;
		}

		b {
			font-size: 140px;
			font-weight: 400;
			font-style: italic;

			@include miniLaptop {
				font-size: 110px;
			}

			@include tabletMaxi {
				font-size: 100px;
			}

			@media (min-width: 1024px) and (max-height: 810px) {
				margin-top: 0;
			}

			@include mobile {
				// margin-top: 8px;
				font-size: 60px;
			}
		}

		@media (min-width: 1024px) and (max-height: 745px) {

			.h1,
			h1 {
				font-size: 60px;
			}

			span {
				font-size: 60px;
			}

			b {
				font-size: 100px;
			}
		}
	}

	&_txt {
		font-size: 20px;
		font-weight: 400;
		line-height: 1.3;

		&-desc {
			font-size: 16px;
			font-style: italic;
			font-weight: 300;
			line-height: 1.1;
		}

		@include mobile {
			font-size: 16px;
		}
	}

	&_btn {
		@media (min-width: 1024px) and (max-height: 745px) {
			margin-top: 15px;
		}
	}
}