.h-banner-map {
	opacity: 0;
	pointer-events: none;

	&.is-active {
		// opacity: 1;
		pointer-events: auto;
	}

	//not a shadow, "mask" maybe ?
	&_shadow {
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			// z-index: -1;
			background: #000;
			opacity: 0.6;
			transition: all 0.3s ease-in-out;
			z-index: 1;
		}

		// is-hidden
		&.is-shadow-hidden {
			[data-banners='mapOverlayText'] {
				pointer-events: none;
			}

			&:before {
				opacity: 0;
				pointer-events: none;
			}
		}
	}

	[data-banners='mapOverlayText'] {
		// transform: translate3d(100%, 0, 0);
	}

	[data-banners='mapContent'] {
		transform: translate3d(100%, 0, 0);

		@include mobile {
			transform: none;
		}
	}
}

[data-banners='hint'],
[data-banners='btnToHint'] {
	&.is-hidden {
		display: none;

		@include mobile {
			display: block;
		}
	}

	&.is-visible {
		display: inline-block;
	}
}

[data-banners='nameCountry'],
[data-banners='name'],
[data-banners='text'],
[data-banners='question'] {
	&.is-hidden {
		display: none;
	}

	&.is-visible {
		display: block;
	}
}

.js-open-hint {
	[data-banners='hint'] {
		display: block;
		opacity: 0;
		transition: all 0.5s ease-in-out;
		transform: translateY(20px);

		@include mobile {
			opacity: 1;
			transform: translateY(0);
		}
	}

	[data-banners='btnToHint'] {
		transform: translateY(-20px);
		opacity: 0;
		transition: all 0.5s ease-in-out;

		@include mobile {
			transform: translateY(0);
			opacity: 1;
			background: #fdf6ed url(../img/icon/ic-close.svg) center center no-repeat;
		}
	}

	[data-banners='btnToHint'].btn.btn-st,
	[data-banners='btnToHint'].btn.btn-st:hover {
		@include mobile {
			background: #fdf6ed url(../img/icon/ic-close.svg) center center no-repeat;
		}
	}

	&.js-open-hint-anim {
		[data-banners='hint'] {
			opacity: 1;
			transform: translateY(0);

			@include mobile {
				opacity: 1;
				transform: translateY(0);
				clip-path: inset(0 0 0 0);
				// animation: slideAndGrow 1s ease-out forwards;
			}
		}

		[data-banners='btnToHint'] {
			display: none;
			transform: translateY(0);
			opacity: 1;

			@include mobile {
				display: block;
				transform: translateY(0);
				opacity: 1;
			}
		}
	}

	&.js-open-content {
		[data-banners='hint'] {
			opacity: 0;
			// transform: translateY(-40%);
		}
	}
}

.js-open-btn-next {
	[data-banners='mapList'] {
		display: block;
		background-color: #ccc;
	}

	[data-banners='btnNext'] {
		transform: translateY(0);
		opacity: 1;
	}

	@include mobile {
		[data-banners='btnToHint'] {
			display: none !important;
		}
	}
}

.js-open-content {
	[data-banners='hint'],
	[data-banners='question'] {
		// display: none;
		opacity: 0;
		transform: translateY(-40%);
		transition: all 0.5s ease-in-out;
	}

	[data-banners='btnToHint'] {
		transition: all 0.5s ease-in-out;
		opacity: 0;
	}

	[data-banners='name'],
	[data-banners='nameCountry'] {
		display: block;
		opacity: 0;
		transform: translateY(40%);
		transition: all 0.5s ease-in-out;
	}

	[data-banners='text'] {
		display: block;
		opacity: 0;
		transform: translateY(20px);
		transition: all 0.5s ease-out;
	}

	&.js-open-content-anim {
		[data-banners='hint'],
		[data-banners='question'],
		[data-banners='btnToHint'] {
			display: none;
		}

		[data-banners='text'],
		[data-banners='name'],
		[data-banners='nameCountry'] {
			opacity: 1;
			transform: translateY(0);
		}

		[data-banners='name'] {
			transition-delay: 0.3s;
		}

		[data-banners='text'] {
			transition-delay: 0.3s;
		}
	}
}

