@include mobile {
	[data-banners='mapOverlayText'] {
		font-size: 16px;
	}

	.h-banner-map {
		// opacity: 1 !important;
		// pointer-events: auto !important;
		transform: none;
	}

	.h-banner-map [data-banners='mapContent'] {
		transform: none !important;
	}

	[data-banners='btnToHint'].btn.btn-st {
		position: absolute;
		bottom: 15px;
		top: auto;
		left: auto;
		right: 0px;
		padding: 0;
		margin: 0;
		z-index: 2;
		display: block;

		width: 36px;
		height: 36px;
		text-indent: -9999px;
		overflow: hidden;
		border-radius: 50%;
		border: 2px solid var(--light-gold, #bb8643);
		box-shadow: none;
		background: #fdf6ed url(../img/icon/ic-question.svg) center center no-repeat;

		&:hover {
			border: 2px solid var(--light-gold, #bb8643);
			background: #fdf6ed url(../img/icon/ic-question.svg) center center no-repeat;
		}

		&.is-active {
			background: #fdf6ed url(../img/icon/ic-close.svg) center center no-repeat;
		}
	}

	.hint {
		&[data-banners='hint'] {
			position: absolute;
			z-index: 1;
			bottom: 16px;
			right: 14px;
			left: 13px;
			// animation: slideAndGrow 1s ease-out forwards infinite;
			// transforn-origin: bottom right;
			clip-path: inset(100% 0 0 100%);
			transition: clip-path 0.3s linear;

			overflow: hidden;
			font-size: 14px;
			line-height: 1.2;
			padding: 24px;
		}
	}

	[data-banners='imgContainer'] {
		// height: 40vh;
		overflow: hidden;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&.img-is-contain {
			img {
				object-fit: contain;
			}
		}

		&.img-is-cover {
			img {
				object-fit: cover;
			}
		}
	}

	.xssss {
		height: calc((var(--vh, 1vh) * 70) - 60px + 64px - 90px) !important;

		// height: calc(var(--vh, 1vh) * 100);
		&2 {
			height: calc(30vh);
		}
	}

	[data-banners='infoListItem'] {
		&.is-not-active {
			[data-banners='headerTxt'] {
				display: none;
			}

			[data-banners='text'] {
				display: none;
			}
		}
	}
}

@keyframes slideAndGrow {
	from {
		clip-path: inset(100% 0 0 100%);
	}

	to {
		clip-path: inset(0 0 0 0);
	}
}

