.h-banner_seat-grid-wrapp {
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	top: 0;
	width: 100%;
	display: flex;
	justify-content: center;
	flex-direction: column;
	padding-top: calc(40px + 57px + 100px);
	padding-bottom: 40px;

	@media (max-height: 650px) and (min-width: 1024px) {
		padding-top: calc(40px + 57px + 50px);
	}

	@media (max-width: 1299px) {
		padding-top: calc(30px + 47px + 100px);

		@media (max-height: 650px) and (min-width: 1024px) {
			padding-top: calc(30px + 47px + 50px);
		}
	}

	.h-banner_seat-grid {
		position: relative;
		top: auto;
		bottom: auto;
		right: auto;
		left: auto;
		width: 100%;
		padding-right: 0;
		height: 510px;
		grid-gap: 0;

		.h-banner_seat-left {
			.seat-box-img_overlay {
				object-fit: contain;
				margin-left: -4%;
				max-width: none;
				max-height: 100%;

				.is-seats-hidden-img & {
					opacity: 0;
				}
			}
		}

		.h-banner_seat-right {
			opacity: 0;
		}

		.-busines & {
			padding-left: 0;
			padding-right: 0;
			height: 450px;
		}

		@media (max-width: 1180px) {
			height: 400px;
			padding-left: 30px;

			.-busines & {
				height: 340px;
			}
		}

		@media (max-width: 899px) {
			height: 270px;
			padding-left: 10px;

			.-busines & {
				height: 190px;
			}
		}

		@media (max-height: 790px) {
			height: 470px;

			.-busines & {
				height: 400px;
			}

			@media (max-width: 1180px) {
				height: 400px;

				.-busines & {
					height: 320px;
				}
			}

			@media (max-width: 899px) {
				height: 250px;

				.-busines & {
					height: 200px;
				}
			}
		}

		@media (max-height: 720px) {
			height: 370px;

			.-busines & {
				height: 300px;
			}

			@media (max-width: 899px) {
				height: 250px;

				.-busines & {
					height: 210px;
				}
			}
		}

		@media (max-height: 620px) {
			height: 290px;

			.-busines & {
				height: 250px;
			}

			@media (max-width: 899px) {
				height: 250px;
			}
		}

		@media (max-height: 560px) {
			height: 250px;
		}

		* {
			height: 100%;
		}

		img {
			height: 100%;
		}
	}
}

