h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
	// display: block;
	// margin-bottom: 30px;
}

.h1,
h1 {
	font-family: $font2;

	font-style: italic;
	font-weight: 400;
	font-size: 100px;
	line-height: 1.2;

	&.-regular {
		font-family: $font2;
		font-weight: 400;
		font-style: normal;
	}

	@include miniLaptop {
		font-size: 72px;
	}

	@include tabletMaxi {
		font-size: 62px;
	}

	@include mobile {
		font-size: 56px;
	}
}

.h2,
h2 {
	// font-family: Prossimo;
	font-size: 100px;
	font-style: normal;
	font-weight: 400;
	line-height: 1;

	@include miniLaptop {
		font-size: 72px;
	}

	@include tabletMaxi {
		font-size: 62px;
	}

	&.-sm {
		font-family: $font2;
		font-size: 72px;
		font-weight: 400;
		font-style: italic;
		line-height: 1.25;
		text-transform: none;

		@include miniLaptop {
			font-size: 56px;
		}

		@include tabletMaxi {
			font-size: 52px;
		}
	}
}

.h3,
h3 {
	font-family: $font1;
	font-size: 64px;
	font-weight: 500;
	line-height: 1.56;
	/* 100px */
	text-transform: uppercase;

	@include miniLaptop {
		font-size: 45px;
	}

	@include tabletMaxi {
		font-size: 42px;
	}

	&.-sm {
		font-size: 53px;
		font-style: italic;
		line-height: 1.1;
		text-transform: none;

		@include miniLaptop {
			font-size: 42px;
		}

		@include tabletMaxi {
			font-size: 38px;
		}
	}
}

.h4,
h4 {
	font-family: $font2;
	font-size: 48px;
	font-style: italic;
	font-weight: 400;
	line-height: 1.2;

	@include miniLaptop {
		font-size: 38px;
	}

	@include tabletMaxi {
		font-size: 34px;
	}

	&.-sm {
		font-size: 42px;
	}

	.-lg {
		font-size: 60px;

		@include miniLaptop {
			font-size: 50px;
		}

		@include tabletMaxi {
			font-size: 44px;
		}
	}
}

.h5,
h5 {
	font-family: $font2;
	font-size: 36px;
	font-style: normal;
	font-weight: 400;
	line-height: 1.6;
	/* 57.6px */
	letter-spacing: 0.72px;

	@include miniLaptop {
		font-size: 32px;
	}

	@include tabletMaxi {
		font-size: 28px;
	}
}

.txt-default {
	font-size: 18px;
	line-height: 1.2;

	@include tabletMaxi {
		font-size: 16px;
	}

	p {
		&:not(:last-child) {
			margin-bottom: 22px;
		}
	}
}

.txt-st {
	font-size: 20px;
	line-height: 1.6;

	@include tabletMaxi {
		font-size: 18px;
	}

	@include mobile {
		&.-mob-sm {
			font-size: 16px;
		}
	}

	p {
		&:not(:last-child) {
			margin-bottom: 32px;
		}
	}

	&.-md {
		letter-spacing: 0.4px;
		line-height: 1.4;
	}

	&.-leading-sm {
		line-height: 1.4;
		@include mobile {
			&.-mob-sm {
				line-height: 1.3;
			}
		}
	}

	&.-spacing-lg {
		letter-spacing: 0.4px;
		@include mobile {
			&.-mob-sm {
				letter-spacing: 0px;
			}
		}
	}

	&.-italic {
		// font-family: $font1ri;
		font-style: italic;
	}
}

.txt-md {
	font-size: 24px;
	line-height: 1.4;
	/* 33.6px */
	letter-spacing: 0.48px;

	@include tabletMaxi {
		font-size: 22px;
	}

	p {
		&:not(:last-child) {
			margin-bottom: 34px;
		}
	}
}

.txt-lg {
	font-size: 28px;
	line-height: 1.4;
	/* 33.6px */
	letter-spacing: 0.56px;

	@include tabletMaxi {
		font-size: 26px;
	}

	p {
		&:not(:last-child) {
			margin-bottom: 34px;
		}
	}
}

.sub-tt {
	font-size: 36px;
	font-weight: 300;
	letter-spacing: 1.44px;
	text-transform: uppercase;

	@include tabletMaxi {
		font-size: 32px;
	}

	&.-sm {
		font-size: 24px;
		line-height: 1.2;
		letter-spacing: 2.4px;
		@include miniLaptop {
			font-size: 22px;
		}

		@include tabletMaxi {
			font-size: 20px;
		}
	}
}

.txt-block {
	// h2,
	// h3 {
	// 	font-family: $font2i;
	// 	font-size: 48px;
	// 	font-style: italic;
	// 	font-weight: 400;
	// 	line-height: 1.2;
	// 	margin-bottom: 64px;
	// }
	> p {
		font-size: 20px;
		line-height: 1.6;
	}

	> blockquote {
		// margin-top: 42px;
		p {
			// color: #11174a;
			font-family: $font2;
			font-size: 36px;
			font-style: normal;
			font-weight: 400;
			line-height: 1.6;
			/* 57.6px */
			letter-spacing: 0.72px;
		}
	}

	> img,
	> picture img {
		margin: 80px auto;
		width: 100%;
	}

	picture {
		display: block;
		line-height: 0;
	}

	p,
	ul,
	ol,
	li {
		&:not(:last-child) {
			margin-bottom: 32px;
		}
	}

	b,
	strong {
		font-weight: 700;
	}

	i,
	em {
		font-style: italic;
	}

	a {
		text-decoration: underline;

		&:hover {
			text-decoration: none;
		}
	}
}
