.btn {
	display: inline-block;
	position: relative;
	z-index: 1;
	line-height: 0;
	cursor: pointer;
	border-radius: 0;
	outline: none;
	border: none;
	box-shadow: none;
	text-align: center;
	transition: all 0.2s ease;

	&_default {
		cursor: default;
	}

	&.-up {
		text-transform: uppercase;
	}
}

.btn-chevron {
	color: var(--off-white, #fcfbf8);
	font-family: $font1;
	font-size: 20px;
	line-height: 24px;
	font-weight: 500;
	letter-spacing: 0.8px;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	padding: 10px 10px;
	background: none;

	&.-sm-text {
		font-size: 16px;
		font-weight: 500;
		letter-spacing: 0.64px;
		line-height: 1.1;
	}

	&_text {
		position: relative;
		z-index: 2;
	}

	&_bg {
		z-index: 1;
		transition: all 0.2s ease-in-out;
		color: #bb8643;
		background-color: #bb8643;
		padding: 0 20px;
		background-clip: content-box;
		overflow: hidden;

		&.-full-bg {
			background-clip: border-box;
			overflow: visible;
			background-color: transparent;
			padding: 0;

			svg {
				position: relative;
				left: auto;
				top: auto;
				bottom: auto;
				right: auto;
				width: 100%;
				height: 100%;
			}
		}

		svg {
			&:first-child {
				position: absolute;
				left: 0;
				top: 0;
				bottom: 0;
				width: auto;
				height: calc(100% + 0.2px);
			}
		}

		svg {
			&:last-child {
				transform: scale(-1);
				position: absolute;
				right: 0;
				top: 0;
				bottom: 0;
				width: auto;
				margin-top: -1px;
				height: calc(100% + 1px);
			}
		}

		svg,
		img {
			width: 100%;
			height: 100%;
			display: inline-block;
		}
	}

	&.-pulsing {
		&:after {
			content: '';
			position: absolute;
			z-index: -1;
			top: 50%;
			left: 50%;
			transform: translate(-50%, calc(-50% - 3px)) scale(0.78);
			width: 113%;
			margin: auto;
			height: 115%;
			background-image: url('../img/icon/pulsing-bg.svg');
			background-position: center center;
			background-repeat: no-repeat;
			background-size: 100% 100%;

			animation: {
				name: pulsing;
				duration: 1s;
				timing-function: linear; // ease | ease-in-out
				delay: 0s; //
				iteration-count: infinite; // infinite | <number>
				direction: normal; // reverse; alternate; alternate-reverse;
				fill-mode: none; // none | forwards | backwards | both
				play-state: running; // running | paused
			}
		}
	}

	&.-icon {
		display: flex;
		padding-left: 0;
		padding-right: 0;

		&.-second {
			.btn-chevron_text {
				color: var(--gold, #a1661c);
				font-family: $font1;
				font-size: 16px;
				font-style: normal;
				font-weight: 500;
				line-height: 110%;
				letter-spacing: 0.64px;
			}
		}

		.btn-chevron_icon {
			position: absolute;
			left: 0;
			width: 33%;
			bottom: 0;
			top: 0;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;

			&.-fill-light {
				path {
					// fill: var(--off-white, #fcfbf8) !important;
					stroke: var(--off-white, #fcfbf8) !important;
				}
			}
		}

		.btn-chevron_text {
			margin-left: 25%;
			width: 100%;
		}

		&.-shadow {
			.btn-chevron_text {
				padding-bottom: 4px;
			}

			.btn-chevron_icon {
				padding-bottom: 4px;
			}
		}

		&.-anim {
			&:after {
				content: '';
				position: absolute;
				z-index: -1;
				top: 50%;
				left: 50%;
				transform: translate(-50%, calc(-50% - 3px)) scale(0.78);
				width: 100%;
				margin: auto;
				height: 100%;
				background-image: url('../img/icon/ic-chevron-icon2-hover.svg');
				background-position: center center;
				background-repeat: no-repeat;
				background-size: 100% 100%;

				animation: {
					name: scaleChevron;
					duration: 1s;
					timing-function: linear; // ease | ease-in-out
					delay: 0s; //
					iteration-count: infinite; // infinite | <number>
					direction: normal; // reverse; alternate; alternate-reverse;
					fill-mode: none; // none | forwards | backwards | both
					play-state: running; // running | paused
				}
			}

			&.-shadow {
				&:after {
					width: calc(100% + 29px);
					height: calc(100% + 29px);
				}
			}
		}
	}

	&_icon {
		position: relative;
		z-index: 1;

		&-bg {
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;

			svg {
				width: 100%;
				height: 100%;
				display: inline-block;
			}
		}
	}

	&:hover {
		.btn-chevron_bg {
			color: #a1661c;
			background-color: #a1661c;

			&.-full-bg {
				background-color: transparent;
			}
		}

		&.-pulsing {
			&:after {
				animation: none;
			}
		}
	}

	&.-border {
		.btn-chevron_text {
			color: #a1661c;
		}

		.btn-chevron_bg {
			z-index: 1;
			transition: all 0.2s ease-in-out;
			color: #bb8643;

			background-color: transparent;
			padding: 0;
			background-clip: padding-box;
			overflow: hidden;

			svg {
				&:first-child {
					transform: none;
					height: auto;
					margin: 0;
					position: relative;
					top: auto;
					left: auto;
					right: auto;
					bottom: auto;
				}
			}

			svg {
				&:last-child {
					transform: none;
					height: auto;
					margin: 0;
					position: relative;
					top: auto;
					left: auto;
					right: auto;
					bottom: auto;
				}
			}

			svg,
			img {
				width: 100%;
				height: 100%;
				display: inline-block;
			}
		}
	}
}

@keyframes scaleChevron {
	0% {
		transform: translate(-50%, calc(-50% - 3px)) scale(0.78);
		opacity: 1;
	}

	70% {
		transform: translate(-50%, calc(-50% - 3px)) scale(1);
		opacity: 0;
	}

	100% {
		transform: translate(-50%, calc(-50% - 3px)) scale(0.78);
		opacity: 0;
	}
}

.btn-st {
	border-radius: 2px;
	background: var(--light-gold, #bb8643);
	box-shadow: 0px 4px 4px 0px rgba(17, 23, 74, 0.2);
	padding: 5px 25px;
	color: var(--off-white, #fcfbf8);
	font-family: $font1;
	font-size: 20px;
	font-style: normal;
	font-weight: 500;
	line-height: 22px;
	letter-spacing: 0.8px;
	white-space: nowrap;
	display: inline-flex;
	align-items: center;
	justify-content: center;

	&:hover {
		background: var(--gold, #a1661c);
	}

	&.-border {
		border: 1px solid var(--light-gold, #bb8643);
		background: none;
		color: var(--light-gold, #bb8643);

		&:hover {
			background: var(--light-gold, #bb8643);
			color: var(--off-white, #fcfbf8);
			box-shadow: 0px 4px 4px 0px rgba(#a1661c, 0.2);
		}
	}

	&.-dark {
		background: var(--dark-blue, #11174a);
		color: var(--off-white, #fcfbf8);

		&:hover {
			background: lighten(#11174a, 10%);
		}

		&.-border {
			background: none;
			border-color: var(--dark-blue, #11174a);
			color: var(--dark-blue, #11174a);

			&:hover {
				background: var(--dark-blue, #11174a);
				box-shadow: 0px 4px 4px 0px rgba(var(--dark-blue, #11174a), 0.2);
				color: var(--off-white, #fcfbf8);
			}
		}
	}

	&.-no-shadow {
		box-shadow: none;
	}

	&.-grad-menu {
		background: var(--light-gold, #bb8643);
	}

	&.-sm {
		font-size: 16px;
		font-weight: 500;
		font-family: $font1;
		letter-spacing: 0.64px;
	}
}

.btn-dish {
	display: inline-block;
	color: transparent;

	&.-sm {
		@include mobile {
			svg {
				height: 16px;
				width: 31px;
			}
		}
	}

	&.is-correct {
		color: #000;

		svg mask rect {
			animation: maskAnimation 0.6s ease-out forwards;
		}
	}

	&.is-incorrect {
		&:not(.is-active) {
			color: #e66b6e;

			animation: {
				name: horizontal-shaking;
				duration: 1.2s;
				timing-function: ease-in-out;
				delay: 0.6s;
				iteration-count: 1;
				direction: normal;
				fill-mode: forwards;
				play-state: running;
			}

			transform-origin: center;

			svg mask rect {
				animation: maskAnimation 0.6s ease-out forwards;
			}
		}
	}
}

