.js-click-burger {
	.menu-wrapper {
		opacity: 1;
		transform: translateX(0);
		pointer-events: auto;
	}
}

.menu-wrapper {
	padding-top: 60px;
	// height: 100vh;
	// height: calc(var(--vh, 1vh) * 100);
	position: relative;
	z-index: 991;
	overflow: hidden;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	opacity: 0;
	pointer-events: none;
	// transform: translateX(-100%);
	transition: opacity 0.3s ease-in-out;
}

.menu {
	height: 100%;

	background: var(--gold-gradient, linear-gradient(93deg, #a26d2b 0%, #ca995b 100%));

	@include mobile {
		overflow: auto;
	}

	&_inner {
		display: flex;
		flex-direction: column;
		height: 100%;

		@include mobile {
			height: auto;
			display: block;
		}
	}

	&_items-box {
		flex-grow: 1;
		width: 100%;
		overflow: hidden;
		// height: calc(100vh - 60px - 120px);
		// height: calc((var(--vh, 1vh) * 100) - 60px - 120px);
	}

	&_items {
		display: flex;
		height: 100%;

		@include mobile {
			display: block;
			height: auto;
		}

		&.articles-first {
			.menu_item {
				&.-first {
					order: 3;
				}

				&.-second {
					order: 4;
				}
			}
		}

		&.order-switched {
			.menu_item {
				&.-three {
					order: 3;
				}

				&.-four {
					order: 4;
				}
			}
		}
	}

	&_item {
		flex-shrink: 0;
		width: 25%;
		position: relative;
		transform: skewX(15deg);
		overflow: hidden;

		@include mobile {
			width: 100%;

			&.-left {
				display: none;
			}

			&.-right {
				display: none;
			}
		}

		&:after {
			content: '';
			position: absolute;
			right: 0;
			top: 0;
			bottom: 0;
			width: 3px;
			background: var(--gold-gradient, linear-gradient(93deg, #a26d2b 0%, #ca995b 100%));
			z-index: 1;

			@include mobile {
				left: 0;
				top: auto;
				height: 3px;
				width: auto;
			}
		}

		@include mobile {
			transform: none;
		}

		&.-left {
			margin-left: -25%;
		}

		&.-right {
			order: 5;
		}

		&.-first {
			.menu_link-img {
				// margin-left: 23%;

				img {
					object-position: center center;
				}
			}

			@include mobile {
				.menu_link-img {
					margin-left: 0%;

					img {
						object-position: center center;
					}
				}
			}
		}

		&.-four {
			.menu_link-img {
				img {
					object-position: 83% center;
				}
			}

			@include mobile {
				.menu_link-img {
					img {
						object-position: center center;
					}
				}

				// &:after {
				// 	display: none;
				// }
			}
		}
		&:nth-child(5) {
			@include mobile {
				&:after {
					display: none;
				}
			}
		}

		&.is-active {
			&:after {
				opacity: 0;
			}

			z-index: 1;

			.menu_link-info-center {
				opacity: 0;
			}
		}
	}

	&_link {
		display: block;
		height: 100%;
		width: 40vw;
		margin-left: -7.5vw;
		width: calc(125% + 15vh);
		margin-left: calc(-25% - 4vh);

		width: calc(150% + 15vh);
		margin-left: calc(-25% - 8vh);
		transform: skewX(-15deg);
		z-index: 1;
		overflow: hidden;
		position: relative;
		z-index: 1;

		@include mobile {
			transform: none;
			margin: 0;
			width: 100%;
			min-height: calc(25vh - 87px + 51px);
			min-height: calc((var(--vh, 1vh) * 25) - 87px + 51px);
			display: flex;
			flex-direction: column;
			justify-content: center;
			// height: calc(var(--vh, 1vh) * 100);
		}

		&-img {
			height: 100%;

			picture {
				display: block;
				height: 100%;
				width: 100%;
			}

			img {
				height: 100%;
				width: 100%;
				object-fit: cover;
			}

			@include mobile {
				position: absolute;
				top: 0;
				left: 0;
				bottom: 0;
				right: 0;
			}
		}

		&-info {
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;

			@include mobile {
				position: relative;
				top: auto;
				left: auto;
				right: auto;
				bottom: auto;
				padding: 5px 20px;
				z-index: 3;
			}

			&-center {
				display: flex;
				justify-content: center;
				flex-direction: column;
				align-items: center;
				height: 100%;
				position: relative;
			}

			&-wrapp {
				position: relative;
				padding-bottom: 0;
				transition: all 0.3s ease-in-out;
			}

			&-btns {
				display: flex;
				justify-content: center;
				flex-direction: column;
				align-items: center;
				position: absolute;
				transform: translateY(0px);
				top: 100%;
				left: 0;
				right: 0;
				opacity: 0;
				transition: all 0.3s ease-in-out;

				@include mobile {
					position: relative;
					// transform: rotateX(90deg);
					max-height: 0;
					// opacity: 1;
					transform-origin: center top;
				}

				.btn {
					&:not(:last-child) {
						margin-bottom: fzd(19px);

						@media (min-width: 1440px) {
							margin-bottom: 19px;
						}

						@include mobile {
							margin-bottom: 30px;
						}
					}

					&:last-child {
						@include mobile {
							margin-bottom: 10px;
						}
					}

					@media (min-width: 700px) {
						width: fzd(246px);
						height: fzd(84px);
						font-size: fzd(20px);
						padding-left: fzd(10px);
						padding-right: fzd(10px);
					}

					@media (min-width: 1440px) {
						width: 246px;
						height: 84px;
						font-size: 20px;
						padding-left: 10px;
						padding-right: 10px;
					}

					@include mobile {
						width: 196px;
						height: 63px;
						font-size: 14px;
						letter-spacing: 0.56px;
					}
				}
			}
		}

		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			background: var(--Gold-Gradient, linear-gradient(93deg, #a26d2b 0%, #ca995b 100%));
			z-index: 2;
			pointer-events: none;
			opacity: 0.4;
			transition: all 0.3s ease-in-out;
		}

		&:hover {
			.menu_link-info-wrapp {
				padding-bottom: fzd(150px);

				@media (min-width: 1440px) {
					padding-bottom: 150px;
				}
			}

			.menu_link-info-btns {
				opacity: 1;
				transform: translateY(fzd(-150px));

				@media (min-width: 1440px) {
					transform: translateY(-150px);
				}

				@include mobile {
					transform: rotateX(0deg);
					max-height: 999px;
					opacity: 1;
				}
			}

			&:after {
				opacity: 0;
			}

			.menu_item.-right & {
				&:after {
					opacity: 0.4;
				}
			}

			.menu_item.-left & {
				&:after {
					opacity: 0.4;
				}
			}
		}

		&.is-active {
			&:after {
				opacity: 0;
			}

			.menu_item.-right & {
				&:after {
					opacity: 0.4;
				}
			}

			.menu_item.-left & {
				&:after {
					opacity: 0.4;
				}
			}
		}
	}

	&_tt {
		color: #fff;
		text-align: center;
		// font-size: 24px;
		font-size: fzd(24px);
		font-style: normal;
		font-weight: 500;
		line-height: 110%;
		// letter-spacing: 4.8px;
		letter-spacing: fzd(4.8px);
		// margin: 30px 0;
		margin: fzd(30px) 0;

		@media (min-width: 1440px) {
			font-size: 24px;
			letter-spacing: 4.8px;
			margin: 30px 0;
		}

		@include mobile {
			font-size: 16px;
			font-weight: 500;
			line-height: 1.1;
			letter-spacing: 3.2px;
			margin: 25px 0;
		}

		b {
			color: #fff;
			font-family: $font2;
			// font-size: 50px;
			font-size: fzd(50px);
			font-style: italic;
			font-weight: 400;
			line-height: 110%;
			// margin-top: 16px;
			margin-top: fzd(16px);
			display: block;
			letter-spacing: 0px;

			@media (min-width: 1440px) {
				font-size: 50px;
				margin-top: 16px;
			}

			@include mobile {
				font-size: 36px;
				margin-top: 8px;
			}
		}
	}

	&_footer {
		background: var(--dark-blue, #11174a);
		position: relative;
		padding: 20px 0;
		flex-shrink: 0;
		min-height: 120px;
		display: flex;
		justify-content: center;
		flex-direction: column;

		@include mobile {
			min-height: 87px;
			padding: 15px 0;
		}

		&-grid {
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 0 120px;

			@include mobile {
				display: block;
				text-align: center;
				padding: 0 20px;
			}
		}

		&-tt {
			margin-right: 35px;

			@include miniLaptop {
				.h4 {
					font-size: 30px;
				}
			}

			@include tabletMaxi {
				.h4 {
					font-size: 20px;
				}
			}

			@include mobile {
				margin-right: 0;

				.h4 {
					font-size: 20px;
					margin-bottom: 8px;
				}
			}
		}
	}

	&_flower-left {
		position: absolute;
		bottom: 0;
		left: 0;
		top: 0;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;

		img,
		svg,
		picture {
			display: inline-block;
			max-height: 100%;
		}

		@include mobile {
			width: 50px;
		}
	}

	&_flower-right {
		position: absolute;
		bottom: 0;
		right: 0;
		top: 0;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		align-items: flex-end;

		img,
		svg,
		picture {
			display: inline-block;
			max-height: 100%;
		}

		@include mobile {
			width: 50px;
		}
	}
}

.logo-menu {
	&.is-hidden {
		pointer-events: none;
	}

	@include mobile {
		display: none;
	}
}
