.mobile-controls-wrap {
    display: none;
}

@include tablet {
    .mobile-controls-wrap {
        display: block;
    }
}

.espn-custom .controls {
    position: fixed;
    bottom: 0;
    left: 0;
    width: calc(100% - 30px);
    height: 60px;
    z-index: 10;
    transform: translateY(120%)translateZ(1px);
    will-change: transform
}

@media screen and (max-width:779px) {
    .espn-custom .controls {
        cursor: grab
    }
}

.espn-custom .controls.in-player .controls__marker:hover .controls__pulse {
    transition: all .25s ease-out, opacity .25s linear;
    transform: translate(-50%, -50%)scale(.45);
    opacity: 1;
    visibility: visible;
    background-color: rgba(255, 255, 255, .13)
}

.espn-custom .controls.in-player .controls__marker:hover .controls__callout {
    opacity: 1;
    visibility: visible
}

.espn-custom .controls.in-player .controls__marker:hover .controls__bg {
    background-color: #ff022f
}

.espn-custom .controls.in-player .controls__marker:hover .controls__callout:before {
    top: 0;
    height: 100%;
    transition: height .3s ease-in-out, top .3s ease-in-out
}

.espn-custom .controls.in-player .controls__marker:hover path.stroke {
    stroke: #fff
}

.espn-custom .controls.in-player .controls__marker:hover path.fill {
    fill: #fff
}

.espn-custom .controls.in-player .controls__marker.active.on:hover .controls__bg {
    background-color: #ff022f
}

.espn-custom .controls.in-player .controls__marker.active.on:hover .controls__pulse {
    background-color: rgba(255, 2, 47, .2)
}

.espn-custom .controls.in-player .controls__marker.active:hover path.fill {
    fill: #fff
}

.espn-custom .controls.in-player .controls__marker.active:hover path.stroke {
    stroke: #fff
}

.espn-custom .controls.in-player .controls__marker.active:hover .controls__bg {
    background-color: #ff022f
}

.espn-custom .controls.in-player .controls__marker.active:hover .controls__pulse {
    background-color: rgba(255, 2, 47, .2)
}

.espn-custom .controls.in-player .controls__marker.on:hover .controls__pulse {
    background-color: rgba(255, 2, 47, .2)
}

.espn-custom .controls.grabbing {
    cursor: grabbing
}

.espn-custom .controls.grabbing .controls__timecode-value {
    transition: all .15s linear .1s, transform .2s ease-in-out .1s;
    opacity: 1;
    visibility: visible;
    transform: translateX(-50%)translateY(0)
}

.espn-custom .controls__title {
    color: #81889c;
    display: inline-block
}

@media screen and (min-width:780px) {
    .espn-custom .controls__title {
        cursor: pointer;
        transition: color .1s linear
    }
}

@media screen and (min-width:780px) {
    .no-touch .espn-custom .controls__title:hover {
        color: #fff
    }
}

.espn-custom .controls__tabs {
    display: flex;
    height: 100%
}

.espn-custom .controls__tab {
    position: relative;
    background-color: #041134;
    font-family: bentonsans, sans-serif;
    font-weight: 400;
    font-size: 10px;
    line-height: 150%;
    padding-left: 10px;
    user-select: none;
    -webkit-user-select: none;
    border-right: 1px solid #2a3552;
    transition: background-color .1s linear, border-color .1s linear
}

.espn-custom .controls__tab-top {
    background-color: #041134;
    position: absolute;
    top: -7px;
    width: calc(100% - 13px);
    height: 8px;
    left: 7px;
    border-top: 1px solid #2a3552;
    transition: background-color .1s linear, border-color .1s linear
}

.espn-custom .controls__tab-corner {
    width: 10px;
    height: 10px;
    background-color: #041134;
    position: absolute;
    top: -5px;
    transform: rotate(45deg);
    transition: background-color .1s linear, border-color .1s linear
}

.espn-custom .controls__tab-corner:nth-child(2) {
    left: 2px;
    border-left: 1px solid #2a3552
}

.espn-custom .controls__tab-corner:nth-child(3) {
    right: 1px;
    border-top: 1px solid #2a3552
}

@media screen and (min-width:780px) {
    .espn-custom .controls__tab:nth-last-child(2) {
        border-right: 0
    }
}

.espn-custom .controls__tab:nth-last-child(2)::after {
    content: '';
    position: absolute;
    left: 100%;
    top: 0;
    height: 100%;
    width: 30px;
    background-color: #041134;
    transition: background-color .1s linear
}

.espn-custom .controls__tab:nth-last-child(2) .controls__tab-top {
    width: calc(100% + 21px)
}

.espn-custom .controls__tab:nth-last-child(2) .controls__tab-corner:nth-child(3) {
    right: -25px;
    display: none
}

@media screen and (max-width:779px) {
    .espn-custom .controls__tab:nth-child(2) {
        border-left: 1px solid #2a3552
    }
}

@media screen and (min-width:780px) {
    .espn-custom .controls__tab:nth-child(2) .controls__tab-top {
        left: 0;
        width: calc(100% - 5px)
    }
}

@media screen and (min-width:780px) {
    .espn-custom .controls__tab:nth-child(2) .controls__tab-corner:nth-child(2) {
        display: none
    }
}

.espn-custom .controls__tab-blank {
    position: relative;
    background-color: #fff;
    width: 50vw;
    flex: 0 0 50vw;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: hco tungsten, sans-serif;
    font-weight: 700;
    font-size: 18px;
    letter-spacing: .01em;
    text-transform: uppercase;
    color: #041134
}

@media screen and (min-width:780px) {
    .espn-custom .controls__tab-blank {
        display: none
    }
}

@media screen and (max-width:779px) {
    .espn-custom .controls__tab-blank .controls__tab-top {
        left: -2px;
        width: calc(100% - 5px)
    }
}

.espn-custom .controls__tab-blank .controls__tab-top,
.espn-custom .controls__tab-blank .controls__tab-corner {
    background-color: #fff;
    border-color: #fff
}

@media screen and (max-width:779px) {
    .espn-custom .controls__tab-blank .controls__tab-corner:nth-child(3) {
        right: 2px
    }
}

.espn-custom .controls__tab-blank div:nth-child(4) {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    flex: 0 0 calc(100% - 60px);
    visibility: hidden
}

.espn-custom .controls__tab-blank div:nth-child(5) {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 60px;
    background-color: #fff
}

.espn-custom .controls__tab-blank div:nth-child(5) svg {
    transition: transform .2s ease-in-out;
    transform-origin: center;
    display: none
}

.espn-custom .controls__tab-blank:nth-child(8) {
    display: flex;
    justify-content: space-between;
    cursor: pointer
}

@media screen and (min-width:780px) {
    .espn-custom .controls__tab-blank:nth-child(8) {
        display: none
    }
}

.espn-custom .controls__tab-blank:nth-child(8) .controls__tab-top {
    left: 7px;
    width: calc(100% - 67px)
}

.espn-custom .controls__tab-blank:nth-child(8) div:nth-child(5) {
    height: calc(100% + 14px)
}

.espn-custom .controls__tab.active {
    background-color: #fff;
    border-right-color: #fff
}

.espn-custom .controls__tab.active .controls__title {
    cursor: default
}

@media screen and (min-width:780px) {
    .no-touch .espn-custom .controls__tab.active .controls__title:hover {
        color: #81889c
    }
}

.espn-custom .controls__tab.active .controls__tab-top {
    border-top-color: #fff
}

.espn-custom .controls__tab.active .controls__tab-corner:nth-child(2) {
    border-left-color: #fff
}

.espn-custom .controls__tab.active .controls__tab-corner:nth-child(3) {
    border-top-color: #fff
}

.espn-custom .controls__tab.active .controls__tab-top,
.espn-custom .controls__tab.active .controls__tab-corner {
    background-color: #fff
}

.espn-custom .controls__tab.active::after {
    background-color: #fff
}

.espn-custom .controls__bar {
    position: absolute;
    // top: 50%;
    left: calc(50vw - 5px);
    width: 1440px;
    height: 20px;
    // transform: translateY(-50%);
    z-index: 6;
    overflow: hidden;

    top: 29.5px;
}

@media screen and (min-width:780px) {
    .espn-custom .controls__bar {
        left: 0;
        width: 100%
    }
}

.espn-custom .controls__total {
    width: 100%;
    height: 1px;
    background-color: #2a3552;
    position: absolute;
    top: 10px;

    background: transparent;
}

.espn-custom .controls__load,
.espn-custom .controls__progress {
    position: absolute;
    top: 10px;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $gold;
    z-index: 3;
    transform: translateX(-100%)
}

.espn-custom .controls__load {
    background-color: #4f5a76;
    z-index: 2
}

.espn-custom .controls__bg {
    position: absolute;
    width: 20px;
    height: 20px;
    background-color: #4f5a76;
    border-radius: 50%;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    z-index: 3;
    transition: background-color .2s linear
}

.espn-custom .controls__pulse {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)scale(.1);
    background-color: rgba(255, 255, 255, .6);
    border-radius: 50%;
    width: 100px;
    height: 100px;
    transition: all .25s ease-in, opacity .25s linear;
    transform-origin: center;
    opacity: 0;
    visibility: hidden;
    z-index: 4;
    pointer-events: none
}

.espn-custom .controls__callout {
    position: absolute;
    top: -35px;
    left: 11px;
    transform: translateY(-100%);
    height: 100px;
    padding-left: 6px;
    width: 130px;
    z-index: 10;
    color: #000;
    opacity: 0;
    visibility: hidden;
    cursor: pointer;
    user-select: none;
    transition: all .4s linear
}

@media all and (min-width:800px) and (min-height:700px) {
    .espn-custom .controls__callout {
        width: 170px;
        height: 130px;
        padding-left: 8px
    }
}

.espn-custom .controls__callout:before {
    content: "";
    position: absolute;
    top: 100%;
    left: -1px;
    width: 1px;
    height: 0;
    background-color: #ff022f;
    transition: height .25s ease-in-out, top .25s ease-in-out
}

.espn-custom .controls__overline {
    font-family: bentonsansmedium, sans-serif;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    margin-top: -4px;
    color: #fff;
    opacity: .7;
    text-shadow: 0 2px 3px rgba(0, 0, 0, .25)
}

@media all and (min-width:800px) and (min-height:700px) {
    .espn-custom .controls__overline {
        font-size: 14px
    }
}

.espn-custom .controls__underline {
    font-family: hco tungsten, sans-serif;
    font-weight: 700;
    font-size: 38px;
    line-height: 85%;
    text-transform: uppercase;
    color: #fff;
    margin-top: 2px;
    text-shadow: 0 2px 4px rgba(0, 0, 0, .2)
}

@media all and (min-width:800px) and (min-height:700px) {
    .espn-custom .controls__underline {
        font-size: 50px
    }
}

.espn-custom .controls__marker {
    position: absolute;
    width: 20px;
    height: 20px;
    top: 50%;
    transform: translateY(-50%)scale(0);
    left: 0;
    z-index: 10;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center
}

.espn-custom .controls__marker:last-child .controls__callout {
    right: 10px;
    left: auto;
    text-align: right;
    padding-left: 0;
    padding-right: 6px
}

.espn-custom .controls__marker:last-child .controls__callout:before {
    left: auto;
    right: -1px
}

.espn-custom .controls__marker svg {
    fill: none;
    z-index: 4
}

.espn-custom .controls__marker path {
    transition: fill .2s linear
}

.espn-custom .controls__marker path.stroke {
    stroke: #fff
}

.espn-custom .controls__marker path.fill {
    fill: #fff
}

.espn-custom .controls__marker.active .controls__bg {
    background-color: #e6e7eb
}

.espn-custom .controls__marker.active .controls__pulse {
    background-color: rgba(255, 2, 47, .2)
}

.espn-custom .controls__marker.active path.stroke {
    stroke: #041134
}

.espn-custom .controls__marker.active path.fill {
    fill: #041134
}

.espn-custom .controls__marker.active.on .controls__bg {
    background-color: #ff022f
}

.espn-custom .controls__marker.active.on .controls__pulse {
    background-color: rgba(255, 2, 47, .2)
}

.espn-custom .controls__marker.active.on .controls__callout:before {
    top: 0;
    height: 100%;
    transition: height .3s ease-in-out, top .3s ease-in-out
}

.espn-custom .controls__marker.active.on path.stroke {
    stroke: #fff
}

.espn-custom .controls__marker.active.on path.fill {
    fill: #fff
}

.espn-custom .controls__marker.on .controls__bg {
    background-color: #ff022f
}

.espn-custom .controls__marker.on .controls__pulse {
    background-color: rgba(255, 2, 47, .2)
}

.espn-custom .controls__marker.on path.stroke {
    stroke: #fff
}

.espn-custom .controls__marker.on path.fill {
    fill: #fff
}

.espn-custom .controls__marker.pulse .controls__pulse {
    transition: all .25s ease-out, opacity .25s linear;
    transform: translate(-50%, -50%)scale(.45);
    opacity: 1;
    visibility: visible;
    background-color: rgba(255, 2, 47, .2)
}

.espn-custom .controls__marker.pulse .controls__callout {
    opacity: 1;
    visibility: visible
}

.espn-custom .controls__marker.pulse .controls__callout:before {
    top: 0;
    height: 100%;
    transition: height .3s ease-in-out, top .3s ease-in-out
}

.espn-custom .controls__timecode {
    position: absolute;
    top: -35px;
    left: 50vw;
    z-index: 30;
    width: 400vw;
    transform: translateX(-100%)
}

@media screen and (min-width:780px) {
    .espn-custom .controls__timecode {
        left: 0;
        width: 100%
    }
}

.espn-custom .controls__timecode-value {
    font-family: bentonsansmedium, sans-serif;
    position: absolute;
    left: 100%;
    transition: all .15s linear, transform .2s ease-in-out;
    transform: translateX(-50%)translateY(4px);
    text-shadow: 0 2px 3px rgba(0, 0, 0, .25);
    text-align: center;
    width: 70px;
    user-select: none;
    -webkit-user-select: none;
    opacity: 0;
    visibility: hidden
}

@media screen and (max-width:779px) {
    .espn-custom .controls__diamonds {
        position: absolute;
        left: 0;
        width: 400vw;
        height: 60px
    }
}

@media screen and (max-width:779px) {
    .espn-custom .controls__markers {
        position: absolute;
        left: 50vw;
        width: 400vw;
        height: 60px
    }
}

.espn-custom .controls__diamond {
    position: absolute;
    width: 7px;
    height: 4px;
    z-index: 10;
    top: 8.5px;
    left: 0;
    opacity: 0;
    background-color: #4f5a76;
    border-radius: 3px;
    overflow: hidden;
    -webkit-backface-visibility: hidden
}

@media screen and (min-width:780px) {
    .espn-custom .controls__diamond {
        top: calc(50% - 1.5px)
    }
}

.espn-custom .controls__diamond-progress {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 5px;
    background-color: #ff022f;
    transform: translateX(-100%);
    z-index: 1
}

.espn-custom .controls__diamond svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 7px;
    height: 7px
}

.espn-custom .controls__diamond rect {
    fill: #4f5a76;
    transition: fill .15s linear
}

.espn-custom .controls__diamond.active {
    display: block
}

.espn-custom .controls__diamond.on rect {
    fill: #ff022f
}

.espn-custom .controls__thumb {
    position: absolute;
    top: 0;
    left: 0;
    height: 11px;
    width: 100%;
    cursor: pointer;
    transform: translateX(-100%);
    z-index: 20;
    user-select: none;
}

@media screen and (min-width:780px) {
    .no-touch .espn-custom .controls__thumb:hover:before {
        opacity: .25;
        transform: scale(2.4)
    }
}

@media screen and (min-width:780px) {
    .no-touch .espn-custom .controls__thumb:hover:after {
        transform: scale(1.5)
    }
}

@media screen and (min-width:780px) {
    .espn-custom .controls__thumb::before {
        content: '';
        position: absolute;
        top: 6.5px;
        left: 100%;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        margin-left: -4px;
        background-color: #ff022f;
        transform: scale(1);
        opacity: 0;
        transition: opacity .15s ease-in-out, transform .15s ease-in-out
    }
}

.espn-custom .controls__thumb::after {
    content: '';
    position: absolute;
    left: 100%;
    top: 5px;
    height: 11px;
    width: 1px;
    background-color: #ff022f;

    background: $gold !important;
    margin-left: 0px;
    box-shadow: none !important;
    width: 10px !important;
    height: 10px !important;
    border-radius: 50%;
}

@media screen and (min-width:780px) {
    .espn-custom .controls__thumb::after {
        top: 6.5px;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        margin-left: -4px;
        transition: transform .15s ease-in-out
    }
}