.container {
	width: 100%;
	// max-width: $container;
	max-width: none;
	padding: 0 100px;
	margin: 0 auto;

	@include tabletMaxi {
		padding: 0 20px;
	}

	@include mobile {
		padding: 0 15px;
	}
}

.grid-row {
	grid-template-columns: repeat(12, minmax(0, 1fr));
	display: grid;
	grid-column: span 12 / span 12;
	grid-column-gap: 12px;
	grid-row-gap: 0px;
	padding-left: 60px;
	padding-right: 60px;

	@include tabletMaxi {
		padding-left: 20px;
		padding-right: 20px;
	}

	@include mobile {
		padding-left: 0;
		padding-right: 0;
		display: block;
	}
}

.full-screen {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	// line-height: 0;
}

.indent-top-negative {
	margin-top: calc(-762 / 1318 * 42%);
	padding-top: calc(762 / 1318 * 42%);
}

.indent-top-negative2 {
	margin-top: calc(-892 / 1222 * 75%);
	padding-top: calc(892 / 1222 * 75%);
}

.indent-top-negative3 {
	margin-top: calc(-492 / 1222 * 75%);
	padding-top: calc(492 / 1222 * 75%);
}

.leading-0 {
	line-height: 0;
}

.mask {
	position: absolute;
	left: 0;
	top: 0;
}
