/*
  This mixin can be used to set the object-fit:
  @include object-fit(contain);

  or object-fit and object-position:
  @include object-fit(cover, top);
*/
[data-anim-fadein] {
  opacity: 0;
}

.paralax-image > * {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.paralax-image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

body:not(.is-touch) .paralax-image_img[data-scroll] {
  will-change: transform;
  transform: translateY(-30%);
}

body:not(.is-touch) .paralax-image_img[data-scroll].is-inview {
  transform: translateY(30%);
}

body:not(.is-touch) .paralax-image > * {
  transform: scale(1.3);
}

.fadeIn {
  opacity: 0;
  transform: translateY(60px);
  transition: 1s 0.1s cubic-bezier(0.33, 1, 0.68, 1);
}

.fadeIn.is-inview {
  opacity: 1;
  transform: translateY(0px);
}

[data-splash='cloud'] {
  transform: translate3d(0, 30px, 0);
}

[data-splash='subTitle'],
[data-splash='title'],
[data-splash='subTitle'],
[data-splash='text'],
[data-splash='btn'],
[data-splash='textDesc'] {
