/* tooltip */
.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted var(--primary-color-black);
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: var(--primary-color-black);
  color: var(--primary-color-white);
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  /* Position the tooltip */
  position: absolute;
  z-index: 1;
  top: 100%;
  left: 50%;
  margin-left: -60px;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

.tooltiptext {
  background-color: var(--primary-color-cnn-black);
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: space-evenly;
  align-items: center;
  padding: 10px 30px;
  line-height: 15px;
  font-style: italic;
  position: fixed;
  opacity: 0;
  height: auto;
  transition: all 1s;
  margin-top: -50px;
  z-index: -1;
  width: auto;
}

@media screen and (min-width: 1024px) {
  .tooltiptext {
    width: 100%;
    padding: 10px 0;
  }
}

.tooltiptext.tooltip--show {
  opacity: 1;
  margin-top: 0;
  transition: all 1s;
}

.tooltip-js {
  cursor: help;
}

