.bg-aisle-business {
	background: #fdf6ed url(../img/dta/bg.jpg) no-repeat center center / cover;
}

.bg-aisle-premium {
	background: #fdf6ed url(../img/dta/bg-premium.jpg) no-repeat center center / cover;
