.dta {
	// background: #fdf6ed url(../img/dta/bg.jpg) no-repeat center center / cover;

	&_seats {
		> div {
			flex-shrink: 0;
		}
	}

	.h4.-lg {
		@media (max-height: 650px) and (min-width: 1024px) {
			margin-bottom: 50px;
		}
	}

	.swiper {
		width: 100%;
		overflow: visible;
		position: static;

		&-slide {
			opacity: 0.2;
			transition: opacity 0.7s;

			&.swiper-slide-active {
				opacity: 1;
			}
		}
	}

	.swiper-horizontal > .swiper-scrollbar,
	.swiper-scrollbar.swiper-scrollbar-horizontal {
		bottom: 0px;
		width: 606px;
		left: 50%;
		transform: translateX(-50%);
		background: rgba(#a1661c, 0.2);

		.swiper-scrollbar-drag {
			background: var(--Gold-Gradient, linear-gradient(93deg, #a26d2b 0%, #ca995b 100%));
		}
	}

	.seats-states {
		img {
			transition: opacity 0.5s;
		}

		&.is-switched {
			.seats-states_1 {
				opacity: 0;
			}

			.seats-states_2 {
				opacity: 1;
			}
		}
	}

	.btn-seat {
		width: 78px;
		height: 78px;
		background: url(../img/icon/icon-shopping.svg) no-repeat center center / contain;
		position: relative;
		left: 0%;
		top: 0%;
		border-radius: 50%;

		&.-wifi {
			background-image: url(../img/icon/icon-wifi.svg);
		}

		&::before {
			content: '';
			@extend .full-screen;
			background: rgba(#bb8643, 1);
			border-radius: 50%;

			z-index: -1;
			transform: scale(0.78);

			animation: {
				name: bg-pulsing;
				duration: 1.5s;
				timing-function: linear; // ease | ease-in-out
				delay: 0s; //
				iteration-count: infinite; // infinite | <number>
				direction: normal; // reverse; alternate; alternate-reverse;
				fill-mode: none; // none | forwards | backwards | both
				play-state: running; // running | paused
			}

			@keyframes bg-pulsing {
				0% {
					transform: scale(0.85);
					opacity: 1;
				}

				70% {
					transform: scale(1.55);
					opacity: 0;
				}

				100% {
					transform: scale(0.78);
					opacity: 0;
				}
			}
		}
	}

	.seat-click {
		position: absolute;
		left: 53%;
		top: -7%;

		&.-wifi {
			left: 40%;
		}

		.seat-info.-st {
			top: 50%;
			left: 50%;
			z-index: -1;
		}
	}

	.seat-info {
		&.-st {
			box-sizing: content-box;
			position: absolute;
			transform: translateY(-100%);
			top: 0;
			left: 30%;
			padding: 0 0 73px 110px;
			background: url(../img/dta/info-line.svg) no-repeat left bottom;
			transition: 0.5s;
			opacity: 0;
			pointer-events: none;
		}

		&.-grid {
			display: flex;
			align-items: center;
			padding-left: 61px;
			position: relative;
			z-index: 2;

			@include miniLaptop {
				// padding-left: 0;
				max-width: 650px;
			}

			&:before {
				content: '';
				position: absolute;
				left: 0;
				top: 90px;
				width: 62px;
				height: 80px;
				background-image: url(../img/dta/info-line2.svg);

				@include miniLaptop {
					// display: none;
				}
			}
		}

		&.-visible {
			position: absolute;
			transform: translateY(-100%);
			top: 0;
			left: 30%;
			transition: 0.5s;

			// opacity: 0;
			// pointer-events: none;
			@include miniLaptop {
				left: 0;
			}
		}

		&_pic {
			width: 180px;
			height: 180px;
			border-radius: 50%;
			overflow: hidden;
			flex-shrink: 0;
			margin-right: 35px;
			position: relative;
			z-index: 1;

			&.is-play {
				.btn-play-video {
					opacity: 0;
				}
			}

			.seat-video {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: top center;
				background-color: #000;
			}
		}

		&.is-active {
			opacity: 1;
			pointer-events: auto;
		}
	}

	.swiper-slide:not(.swiper-slide-active) {
		.seat-info {
			opacity: 0;
			pointer-events: none;
		}
	}

	&.-busines-horizontal {
		.swiper-slide:not(.swiper-slide-active) {
			.seat-drag,
			.seat-click {
				opacity: 0;
			}
		}
	}

	.swiper-slide:not(.swiper-slide-active) {
		.seat-info {
			opacity: 0;
			pointer-events: none;
		}
	}

	.swiper-slide-active {
		.seat-box-img_overlay {
			opacity: 0;
			pointer-events: none;
		}
	}

	.seat-drag {
		// position: absolute;
		// left: -19%;
		// top: 15%;
		// z-index: 2;
		// &.-second {
		// 	left: 50%;
		// 	top: 7%;
		// }

		&.is-hidden {
			transition: 0.4s;
			opacity: 0;
		}
	}

	.toggle-button {
		cursor: move;
		background: url(../img/dta/drag-circle.svg) no-repeat center center;
		display: block;
		height: 24px;
		position: absolute;
		top: -12px;
		// transition: left 0.4s ease-out;
		left: -12px;
		width: 24px;
		z-index: 1;
		transform-origin: center center;

		&.-pulse {
			animation: pulse 2s infinite;
			border-radius: 50%;
		}
	}

	// .toggle-switch.-second {
	// 	width: 100%;
	// 	height: 100%;
	// 	position: absolute;
	// 	top: 0;
	// 	left: 0;
	// 	bottom: 0;
	// 	right: 0;
	// 	.toggle-button {
	// 		top: 0;
	// 	}
	// }

	// .seat-headphones {
	// }

	.seat-lottie {
		position: absolute;
		// top: -9%;
		// left: 38%;
		width: 150px;
		pointer-events: none;
		z-index: 111;
		transition: all 0.3s ease-in-out;
		opacity: 1;
		&.-flash {
			width: 436px;
			width: 102%;
			height: 467px;
			height: 102%;
			max-width: none !important;
		}
	}

	.swiper-slide:not(.swiper-slide-active) {
		.seat-lottie {
			opacity: 0;
		}
	}

	.seat-window {
		left: 50%;
		margin-left: -10%;
		right: auto;
		width: 55%;
		// max-width: none;
		svg,
		img {
			// max-width: none;
		}
	}
}

[data-path-draggable-container] {
	&.is-toggled {
		.seat-drag {
			transition: 0.4s;
			opacity: 0;
		}

		.seat-info {
			opacity: 1;
			pointer-events: auto;
		}

		.seats-states {
			.seats-states_1 {
				opacity: 0;
				transition-timing-function: ease-in;
			}

			.seats-states_2 {
				opacity: 1;
				transition-timing-function: ease-out;
			}
		}

		.seat-headphones {
			opacity: 0;
			pointer-events: none;
		}
	}
}

[data-video-st='secondContainer'] {
	&.is-animate {
		.seats-states {
			.seats-states_1 {
				opacity: 0;
				transition-timing-function: ease-in;
			}

			.seats-states_2 {
				opacity: 1;
				transition-timing-function: ease-out;
			}
		}
	}
}

.seat-box-img {
	position: relative;
	height: 100%;

	&_overlay {
		position: absolute;
		bottom: 0;
		left: 0;
		opacity: 1;
		pointer-events: none;
		transition: all 0.3s ease-in-out;
	}

	&_section {
		&-imgs {
			position: absolute;
			top: 0;
			left: 0;
			// bottom: 2.2%;
			bottom: 0;
			bottom: 0;
			right: 0;
			z-index: -1;

			// width: 100%;
			// height: 100%;
		}
		&-img {
			&1 {
				position: absolute;
				left: 32%;
				bottom: 3.7%;
				width: 100%;
				height: 51%;
			}
			&2 {
				position: absolute;
				left: 0;
				bottom: 2.2%;
				top: -2.2%;
				margin-top: -3%;

				top: 0;
				bottom: 0;
				// right: 0;
				height: 100%;
				left: calc(101% + 10px);
				// opacity: 0;
				.swiper-slide-prev & {
					opacity: 0;
				}
				.swiper-slide-next & {
					opacity: 0;
				}
				.swiper-slide-active & {
					opacity: 1;
				}
				.swiper-slide:last-child & {
					opacity: 0;
				}
			}
		}
	}

	&_section {
		// position: absolute;
		// bottom: 3.6%;
		// left: 50%;
		// height: 50%;
		// width: 80%;
		// max-width: none;
		// opacity: 1;
		// pointer-events: none;
		// transition: all 0.3s ease-in-out;
		// z-index: -1;
		// width: 100%;
		// height: 100%;
		display: none;
		position: absolute;
		bottom: 0;
		right: 0;
		height: 53%;
		&-mask {
			position: absolute;
			bottom: 0;
			right: 0;
			height: 58%;
			width: 37%;
			width: calc(30% + 20px);

			// top: 0;
			// height: 100%;
			// z-index: -1;
			&1 {
				position: absolute;
				bottom: 0;
				left: 0;
				display: none;
				// height: 58%;
				// width: 37%;
				// width: calc(30% + 20px);
			}
		}
		&-box {
			position: absolute;
			top: 0;
			bottom: 3.6%;
			left: 50%;
			// height: 50%;
			width: 86.6%;
			max-width: none;
			opacity: 1;
			pointer-events: none;
			transition: all 0.3s ease-in-out;
			z-index: -1;
			display: none;
			.seat-box-img_section {
				// display: block;
			}
		}
