@keyframes pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(#bb8643, 0.7);
	}

	70% {
		box-shadow: 0 0 0 20px rgba(#bb8643, 0);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(#bb8643, 0);
	}
}

@keyframes pulse-sm {
	0% {
		box-shadow: 0 0 0 0 rgba(#bb8643, 0.7);
	}

	70% {
		box-shadow: 0 0 0 8px rgba(#bb8643, 0);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(#bb8643, 0);
	}
}

