					background-position: left bottom -55px;
				}
			}

			.seat-headphones {
				top: -16%;
				left: -23%;
			}

			.seat-lottie.-sounds {
				top: -22%;
				max-width: 70px;
			}

			.seat-lottie.-message {
				top: -14%;
				max-width: 100px;
			}

			.seat-drag.-volume {
				top: -30%;
			}
		}
	}

	@media (max-height: 560px) {
		.swiper.-h-adaptive {
			height: 220px;

			.seat-lottie.-message {
				top: -17%;
				max-width: 100px;
			}
		}
	}
}

@include miniLaptop {
	.dta {
		.dta_wrapp {
			padding-top: 30px;
		}

		.h4 {
			padding-left: 30px;
			padding-right: 30px;
		}

		.seat-info {
			&.-st {
				left: 0;
				padding: 0 0 73px 110px;
			}

			&.-grid {
				width: 650px;
			}
		}
	}

	@media (max-height: 720px) and (min-width: 1024px) {
		.dta {
			.seat-info {
				font-size: 18px;
				line-height: 1.3;

				&.-st {
					left: 0;
					padding: 0 0 73px 110px;
				}

				&.-grid {
					width: 580px;
					padding-left: 0;

					&:before {
						display: none;
					}
				}
			}

			.seat-info_pic {
				width: 150px;
				height: 150px;
			}
		}
	}
}

// tablet

.dta {
	@include tabletMaxi {
		.swiper.-h-adaptive {
			.seat-info {
				&.-st {
					// padding-bottom: 50px;
					padding-left: 0;
					box-sizing: border-box;
					background: none;
				}
			}

			.seat-info {
				&.-st {
					text-align: center;
					left: 50%;
					width: 80vw;
					transform: translate(-50%, -100%);
				}

				&.-visible {
					left: 50%;
					transform: translate(-50%, -100%);
					top: -7%;
				}

				&.-grid {
					padding-left: 0;

					&:before {
						display: none;
					}
				}
			}

			.seat-click {
				top: -17%;
				left: 50%;

				.seat-info.-st {
					transform: translate(-50%, -100%);
					text-align: center;
				}
			}
		}
	}
}

