// from animation
.dta {
	z-index: 11;

	background: none;
	pointer-events: none;

	.is-seats-animation & {
		pointer-events: auto;
	}

	.h4 {
		opacity: 0;
		transform: translateY(-30px);
	}

	.swiper-container-wrapp {
		// position: absolute;
		// left: calc(50% - 927px);
		// height: 100%;
		width: 100vw;
		min-width: 0;
		min-height: 0;
		display: flex;
		flex-direction: column;
		justify-content: center;
		transform: translate3d(calc(50% - 189px), 0, 0);

		// left: calc(100% - 413px);
		// top: calc(100% - 485px);
		// .swiper-slide {
		// 	transform: scale(1.05);
		// }
		.swiper {
			min-width: 0;
		}
		@media (max-width: 1180px) {
			transform: translate3d(calc(50% - 146px), 0, 0);
		}

		@media (max-width: 899px) {
			transform: translate3d(calc(50% - 106px), 0, 0);

			.swiper-slide {
				transform: scale(0.6);
			}
		}

		@media (max-height: 790px) {
			// left: calc(100% - 372px);
			// top: calc(100% - 357px);
			transform: translate3d(calc(50% - 166px), 0, 0);

			// .swiper-slide {
			// 	transform: scale(1);
			// }
			@media (max-width: 1180px) {
				transform: translate3d(calc(50% - 136px), 0, 0);
			}

			@media (max-width: 899px) {
				transform: translate3d(calc(50% - 90px), 0, 0);

				.swiper-slide {
					transform: scale(0.65);
				}
			}
		}

		@media (max-height: 720px) {
			// left: calc(100% - 372px);
			// top: calc(100% - 357px);
			transform: translate3d(calc(50% - 156px), 0, 0);

			// .swiper-slide {
			// 	transform: scale(1.2);
			// }
			@media (max-width: 1180px) {
				transform: translate3d(calc(50% - 130px), 0, 0);
			}

			@media (max-width: 899px) {
				transform: translate3d(calc(50% - 90px), 0, 0);

				.swiper-slide {
					transform: scale(0.65);
				}
			}
		}

		@media (max-height: 620px) {
			@media (max-width: 1180px) {
				transform: translate3d(calc(50% - 106px), 0, 0);
			}

			@media (max-width: 899px) {
				transform: translate3d(calc(50% - 90px), 0, 0);

				.swiper-slide {
					transform: scale(0.8);
				}
			}
		}

		@media (max-height: 560px) {
			@media (max-width: 899px) {
				transform: translate3d(calc(50% - 90px), 0, 0);

				.swiper-slide {
					transform: scale(0.95);
				}
			}
		}

		// transition: all 1s ease-out;

		// left: 0;
		// top: calc(50% - 147px);

		.is-seats-animation & {
			transition: all 1s ease-out;

			.swiper-slide {
				transform: scale(1);
				transition: all 1s ease-out;
			}

			// left: 0;
			// top: calc(50% - 225px);

			transform: translate3d(0, 0, 0);

			@media (max-height: 790px) {
				height: 400px;
				top: calc(50% - 200px);
			}
		}

		.is-seats-active & {
			transition: all 0s ease-out;
			position: static;
			left: auto;
			top: auto;
		}
	}

	.swiper-slide {
		opacity: 0;

		.is-seats-animation & {
			opacity: 0.4;
		}

		&.swiper-slide-active {
			opacity: 1;
			z-index: 1;
			+ .swiper-slide {
				z-index: 2;
			}

			// pointer-events: auto;
			// transform: translate3d(151%, calc(100vh - 400px), 0);
			// transform: translate(-100%, 0);
		}
	}

	.seat-drag {
		opacity: 0;
	}

	.swiper-slide-active .seat-box-img_overlay {
		opacity: 1;
		transition: all 0s ease-in-out;

		.is-seats-hidden-img & {
			opacity: 0;
			transition: all 0.5s ease-in-out;
		}
	}

	.swiper-slide-active {
		.seat-drag {
			.is-seats-animation & {
				opacity: 1;
			}
		}

		&.is-toggled {
			.seat-drag {
				opacity: 0;
			}
		}
	}

	.seat-info {
		&:not(.-visible) {
			opacity: 0;
		}
	}

	[data-path-draggable-container].is-toggled .seat-info {
		opacity: 0;
	}

	.swiper-slide-active {
		// .seat-info,
		&[data-path-draggable-container].is-toggled .seat-info {
			.is-seats-animation & {
				opacity: 1;
			}
		}
	}

	.seat-window {
		opacity: 0;
		transition: all 1s ease-out;

		.is-seats-animation & {
			opacity: 0.4;
		}
	}

	.swiper-slide-active {
		.seat-window {
			opacity: 0;

			.is-seats-animation & {
				opacity: 1;
			}
		}
	}

	.swiper-scrollbar {
		opacity: 0;

		.is-seats-active & {
			opacity: 1;
		}
	}

	&.-busines,
	&.-premium {
		.swiper-container-wrapp {
			transform: translate3d(calc(50% - 132px), 0, 0);

			@media (max-width: 1180px) {
