				}
			}

			@media (max-height: 790px) {
				transform: translate3d(calc(50% - 120px), 0, 0);

				@media (max-width: 1180px) {
					transform: translate3d(calc(50% - 100px), 0, 0);
				}

				@media (max-width: 899px) {
					transform: translate3d(calc(50% - 60px), 0, 0);

					.swiper-slide {
						transform: scale(0.65);
					}
				}
			}

			@media (max-height: 720px) {
				transform: translate3d(calc(50% - 100px), 0, 0);

				@media (max-width: 1180px) {
					transform: translate3d(calc(50% - 90px), 0, 0);

					.swiper-slide {
						transform: scale(0.95);
					}
				}

				@media (max-width: 899px) {
					transform: translate3d(calc(50% - 65px), 0, 0);

					.swiper-slide {
						transform: scale(0.65);
					}
				}
			}

			@media (max-height: 620px) {
				transform: translate3d(calc(50% - 80px), 0, 0);

				@media (max-width: 1180px) {
					transform: translate3d(calc(50% - 80px), 0, 0);

					.swiper-slide {
						transform: scale(0.79);
					}
				}

				@media (max-width: 899px) {
					transform: translate3d(calc(50% - 90px), 0, 0);

					.swiper-slide {
						transform: scale(0.8);
					}
				}
			}

			@media (max-height: 560px) {
				@media (max-width: 899px) {
					transform: translate3d(calc(50% - 90px), 0, 0);

					.swiper-slide {
						transform: scale(0.95);
					}
				}
			}

			@include mobile {
				transform: translate3d(100%, 0, 0);

				.swiper-slide {
					transform: scale(1);
				}
			}

			// transition: all 1s ease-out;

			// left: 0;
			// top: calc(50% - 147px);

			.is-seats-animation & {
				transition: all 1s ease-out;

				.swiper-slide {
					transform: scale(1);
					transition: all 1s ease-out;
				}

				// left: 0;
				// top: calc(50% - 225px);

				transform: translate3d(0, 0, 0);

				@media (max-height: 790px) {
					height: 400px;
					top: calc(50% - 200px);
				}
			}

			.is-seats-active & {
				transition: all 0s ease-out;
				position: static;
				left: auto;
				top: auto;
			}
		}
	}
}

.-volume {
	&.toggle-switch {
		position: absolute;
		top: 10px;
		left: 7px;
		bottom: 10px;
		right: 18px;
		// background-color: yellow;
		overflow: hidden;
		border-radius: 5px;
		// opacity: 0.5;

		.toggle-button {
			z-index: auto !important;
			top: -21px;
			left: -20px;
			background: none;

			&:hover {
				span {
					background-color: #71a850;
				}
			}

			&.-pulse {
				-webkit-animation: pulse-sm 2s infinite;
				animation: pulse-sm 2s infinite;
			}

			&:before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				bottom: 0;
				right: 0;
				background: url(../img/dta/drag-circle.svg) no-repeat center center;
			}

			span {
				position: absolute;
				right: 50%;
				top: 50%;
				transform: translate(0, -50%);
				width: 240px;
				height: 37px;
				background-color: #bb8643;
				border-radius: 4px;

				z-index: -2;
			}
		}
	}
}
