[data-page-cursor] {

  // &.is-hidden {
  //   opacity: 1;
  //   visibility: visible !important;
  //   left: 50%;
  //   top: 34%;
  //   transform: translateX(-50%);
  // }

  @include transition(opacity);
  opacity: 0;
  // transition-delay: 0.3s;
  will-change: opacity;
  position: fixed;
  top: 0;
  left: 0;
  color: $white;
  z-index: 990;
  pointer-events: none;

  &.is-hidden,
  .is-loading &,
  .hide-custom-cursor & {
    visibility: hidden;
  }

  &.is-stopped {
    opacity: 0 !important;
    visibility: hidden !important;
  }

  .custom-cursor--enabled & {
    opacity: 1;
  }

  .touchevents & {
    display: none;
  }

  &.is-hover--stop,
  &.is-controls-hover,
  &.is-hide {
    .custom-cursor__inner {
      &:before {
        transform: scale(0.5);
        opacity: 0;
      }
    }
  }

  &.is-controls-hover,
  &.is-hide {
    .custom-cursor__dot {
      opacity: 0;
      // &:after {
      //   transform: scale(0.5);
      //   opacity: 0;
      // }
    }
  }

  &.is-hover--arrow-next,
  &.is-hover--arrow-prev,
  &.is-hover--back-home {
    .custom-cursor__inner {
      &:before {
        transform: scale(2);
        background: currentColor;
      }

      &:after {
        transform: scale(1) translate(-50%, -50%);
        width: 2rem;
        height: 2rem;
        background-size: contain;
        background-position: 50% 50%;
        transform-origin: center;
      }
    }

    .custom-cursor__dot {
      opacity: 0;
    }
  }

  &.is-hover--arrow-next {
    .custom-cursor__inner {
      &:after {
        background-image: url("data:image/svg+xml,%3Csvg version='1.1' xmlns='http://www.w3.org/2000/svg' width='1024' height='1024' viewBox='0 0 1024 1024'%3E %3Cpath d='M778.88 573.44l-229.12 220.57 90.24 86.63 384-368.64-384-368.64-90.24 86.63 229.12 220.57h-778.88v122.88h778.88z' fill='%23000000'%3E %3C/path%3E %3C/svg%3E ");
      }
    }
  }

  &.is-hover--arrow-prev {
    .custom-cursor__inner {
      &:after {
        background-image: url("data:image/svg+xml,%3Csvg version='1.1' xmlns='http://www.w3.org/2000/svg' width='1024' height='1024' viewBox='0 0 1024 1024'%3E %3Cpath d='M245.12 573.44l229.12 220.57-90.24 86.63-384-368.64 384-368.64 90.24 86.63-229.12 220.57h778.88v122.88h-778.88z' fill='%23000000'%3E %3C/path%3E %3C/svg%3E ");
      }
    }
  }

  &.is-hover--back-home {
    .custom-cursor__inner {
      &:after {
        content: 'Read the article';
        width: 200%;
        padding-top: 0.6rem;
        color: $gray-900;
        height: 200%;
        display: flex;
        overflow: hidden;
        align-items: center;
        justify-content: center;
        position: absolute;
        font-size: 1.4rem;
        line-height: 1.35;
        text-align: center;
      }
    }
  }

  .is-video-hovered & {
    visibility: hidden;
  }

  &.is-clicked {

    .custom-cursor__wrap {
      // transform: scale(0.85);

    }

    .custom-cursor__dot {
      &::after {
        transform: scale(0.85);
        // opacity: 0;
      }
    }
  }
}

.is-video-hovered {
  .page-video {
    [data-page-cursor] {
      visibility: visible;
    }
  }
}

.custom-cursor__dot {
  width: 17px;
  height: 17px;
  // border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -8.5px;
  margin-left: -8.5px;
  transition: opacity 0.3s;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    border-radius: 50%;
    top: 0;
    width: 100%;
    height: 100%;
    background: currentColor;
  }
}

.custom-cursor__inner {
  display: flex;
  pointer-events: none;
  align-items: center;
  justify-content: center;
  width: 110px;
  height: 110px;
  position: relative;

  &:before {
    @include transition(opacity transform);
    content: '';
    left: 1px;
    right: 1px;
    top: 1px;
    bottom: 1px;
    position: absolute;
    border-radius: 9999px;
    border: solid 2px currentColor;
    background: rgba(black, 0.1) url(../img/icon-click.png) no-repeat center center / 77px auto;
  }

  &:after {
    @include transition(transform);
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: scale(0) translate(-50%, -50%);
  }
}

[data-cursor='arrow-prev'],
[data-cursor='arrow-next'] {
  .is-loaded & {
    cursor: pointer;
    pointer-events: none;
  }

  .prev-slide-active.next-slide-active & {
    pointer-events: auto;
  }
}

[data-cursor='arrow-prev'] {
  .prev-slide-active & {
    pointer-events: auto;
  }

  .next-slide-active & {
    pointer-events: none;
  }
}

[data-cursor='arrow-next'] {
  .prev-slide-active & {
    pointer-events: none;
  }

  .next-slide-active & {
    pointer-events: auto;
  }
}