html,
body {
	height: 100%;
}

html,
body {
	font-family: $font1;
	line-height: 1.2;
	font-size: 18px;
	font-weight: 400;
	color: $c-black;

	@include mobile {
		font-size: 16px;
		line-height: 1.3;
	}
}

main {
	width: 100%;
}

video,
svg {
	display: block;
	max-width: 100%;
	height: auto;
}

img {
	display: inline-block;
	max-width: 100%;
	height: auto;
	vertical-align: bottom;
}

button,
a,
.is-cursor,
[type*='submit'] {
	cursor: pointer;
}