.art-hero {
	&_bg {
		clip-path: inset(59px 100px 77px round 20px);
		@include tabletMaxi {
			clip-path: inset(40px 40px 40px round 20px);
		}
		@include tabletMaxi {
			clip-path: inset(0 0 0 round 0);
		}
	}
	&_tt {
		transform-origin: center left;
	}
	&_txt {
		transform-origin: center left;
	}
}

