[data-video],
.video-stories {
    height: 100%;
}

.video-stories {
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}

[data-videos] {
    &.is-video-2 {
        .video-story-1 {
            // opacity: 0;
            pointer-events: none;
        }

        .video-story-2 {
            opacity: 1;
            pointer-events: auto;
        }
    }

    &.is-hidden {
        [data-video] {
            pointer-events: none;
        }
    }
}

.is-hidden-component {
    opacity: 0;
    pointer-events: none !important;
    transition: 0.5s opacity;
}

.video-story-1 {
    transition: opacity 0.1s;
}

.video-story-2 {
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
}

.plyr {
    $parent: &;
    $transition-time: 0.5s;
    line-height: 1.1;

    &--video video {
        object-position: left 60px;
    }

    .video-segments {
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        display: flex;

        .video-segment {
            width: 200px;
            height: 100%;
            position: relative;
            color: rgba(#FFFFFF, 0.4);
            transition: color $transition-time, background $transition-time;
            background-color: #1b2449;

            @include tablet {
                &:nth-child(1) {
                    .video-segment_title {
                        left: 0;
                    }
                }
            }

            &::after {
                content: '';
                position: absolute;
                left: 0;
                top: 39.5px;
                background-color: rgba(#FFFFFF, 0.4);
                height: 1px;
                width: 100%;
            }

            &:not(:last-child) {
                border-right: 1px solid white;
            }

            &_title {
                font-size: 14px;
                position: absolute;
                left: 28px;
                top: 10px;
            }

            .key-moment {
                position: absolute;
                top: 30px;
                width: 16px;
                height: 11px;
                background: url(../img/icon-key-moment.svg) no-repeat center center / contain;
                z-index: 1;

                &_thumbnail {
                    width: 128px;
                    height: 80px;
                    position: absolute;
                    left: 50%;
                    transform: translateX(-50%) scale(0.9);
                    bottom: 47px;
                    top: auto;
                    border: 2px solid white;
                    border-radius: 10px;
                    overflow: hidden;
                    pointer-events: none;
                    opacity: 0;
                    transition: $transition-time cubic-bezier(0.215, 0.61, 0.355, 1);
                }

                &.is-hovered {
                    .key-moment_thumbnail {
                        opacity: 1;
                        transform: translateX(-50%) scale(1);

                        img {
                            object-position: right center;
                        }
                    }
                }
            }

            .interactive-point {
                position: absolute;
                top: 37.5px;
                width: 27px;
                border-radius: 2.5px;
                height: 5px;
                background: #ADAEC1;
                z-index: 1;
                transition: background $transition-time;

                &_hover-support {
                    content: '';
                    position: absolute;
                    left: 0;
                    right: 0;
                    bottom: 100%;
                    height: 50px;
                }

                &_line {
                    position: absolute;
                    left: 50%;
                    bottom: 100%;
                    height: 50px;
                    background: #BB8643;
                    width: 2px;
                    z-index: 1;
                    transform: scaleY(0);
                    transition: all 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
                    transform-origin: center bottom;
                }

                &_inner {
                    position: absolute;
                    left: 0;
                    top: 0;
                    bottom: 0;
                    width: 100%;
                    background: $gold;
                    border-radius: 2.5px;
                    // transform: scaleX(0);
                    width: 0;
                    transform-origin: left center;
                }

                .btn-interactive {
                    width: 234px;
                    height: 92px;
                    position: absolute;
                    left: 50%;
                    transform: translateX(-50%) translateY(10px);
                    bottom: 55px;
                    top: auto;
                    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.20));
                    z-index: 2;
                    visibility: hidden;
                    opacity: 0;
                    transition: all 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
                    text-align: left;
                    color: var(--gold);

                    &.-article {
                        left: -30%;
                    }

                    &.-long {
                        width: 309px;

                        &::after {
                            background-image: url('../img/bg-pulse-long.svg');
                        }

                        .btn-interactive_text {
                            width: 78%;
                        }
                    }

                    &_text {
                        font-size: 24px;
                        position: absolute;
                        z-index: 1;
                        font-family: $font2Name;
                        font-style: italic;
                        padding: 17px;
                        line-height: 1.2;
                        width: 71%;
                    }

                    .tmln-btn_bg {
                        transition: fill 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
                    }

                    .tmln-btn_stroke {
                        transition: stroke 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
                    }

                    &:hover {
                        color: white;

                        .tmln-btn_bg {
                            fill: var(--light-gold);
                        }

                        .tmln-btn_stroke {
                            stroke: white;
                        }

                        &:after {
                            // opacity: 0 !important;
                            animation: none;
                        }
                    }

                    &:after {
                        content: '';
                        position: absolute;
                        z-index: -1;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, calc(-50% - 3px)) scale(0.78);
                        width: calc(100% + 29px);
                        margin: auto;
                        height: calc(100% + 29px);
                        background-image: url('../img/bg-pulse.svg');
                        background-position: center center;
                        background-repeat: no-repeat;
                        background-size: 100% 100%;
                        pointer-events: none;

                        animation: {
                            name: pulsing;
                            duration: 1.3s;
                            timing-function: linear; // ease | ease-in-out
                            delay: 0s; //
                            iteration-count: infinite; // infinite | <number>
                            direction: normal; // reverse; alternate; alternate-reverse;
                            fill-mode: none; // none | forwards | backwards | both
                            play-state: running; // running | paused
                        }

                        @keyframes pulsing {
                            0% {
                                transform: translate(-50%, calc(-50% - 3px)) scale(0.85);
                                opacity: 1;
                            }

                            70% {
                                transform: translate(-50%, calc(-50% - 3px)) scale(1.05);
                                opacity: 0;
                            }

                            100% {
                                transform: translate(-50%, calc(-50% - 3px)) scale(0.78);
                                opacity: 0;
                            }
                        }
                    }

                    // &.-culinary {
                    //     background: url(../img/icon-culinary.svg) no-repeat center center / 100% auto;

                    //     span {
                    //         background: url(../img/icon-culinary-hover.svg) no-repeat center center / 100% auto;
                    //         opacity: 0;
                    //         transition: 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
                    //     }
                    // }

                    &:hover {
                        span {
                            opacity: 1;
                        }
                    }
                }

                &:hover,
                &.is-hovered,
                &.is-inrange {
                    button {
                        opacity: 1;
                        visibility: visible;
                        transform: translateX(-50%) translateY(0px);
                    }

                    .interactive-point_line {
                        transform: scale(1);
                    }
                }
            }

            &.is-active {
                color: $sia-blue;
                background: white;

                .key-moment {
                    background: url(../img/icon-key-moment-dark.svg) no-repeat center center / contain;
                }

                .interactive-point {
                    background: $sia-blue;
                }

                &::after {
                    background-color: #2a3552;
                }
            }
        }
    }

    @include tablet {
        .plyr__seek-wrap {
            display: none;
        }
    }

    &--video {
        height: 100%;

        video {
            object-fit: cover;
        }

        #{$parent}__controls {
            background: rgba($sia-blue, 0.7);
            padding: 0;
            height: 65px;
            // transform: translateY(100%);
            transition: 0.5s ease-out;
            opacity: 0;

            @include tablet {
                bottom: 0px;
                justify-content: flex-start;
                background: white;
            }

            &.is-initialized {
                transform: none;
                opacity: 1;
            }
        }

        input[type=range]::-webkit-slider-runnable-track {
            background-color: #2a3552 !important;
            background-color: rgba(#FFFFFF, 0.4) !important;
            background-color: transparent !important;
            height: 1px !important;
        }

        input[type=range]::-moz-range-track {
            background-color: #2a3552 !important;
            background-color: rgba(#FFFFFF, 0.4) !important;
            background-color: transparent !important;
            height: 1px !important;
        }

        // input[type=range]:focus-visible::-webkit-slider-runnable-track {
        //     outline: 2px dashed pink !important;
        //     // outline: 2px dashed var(--plyr-focus-visible-color, var(--plyr-color-main, var(--plyr-color-main, #00b2ff)));
        //     outline-offset: 2px
        // }

        input[type=range] {
            color: $gold !important;
            height: 100% !important;
            margin-left: -5px;
            margin-right: -5px;
            padding: 15px 0 0px;
            cursor: pointer;
        }

        input[type=range]::-webkit-slider-thumb {
            background: $gold !important;
            margin-top: -5px;
            box-shadow: none !important;
            width: 10px !important;
            height: 10px !important;
        }

        input[type=range]::-moz-range-thumb {
            background: $gold !important;
            margin-top: -5px;
            box-shadow: none !important;
            width: 10px !important;
            height: 10px !important;
        }

        input[type=range]::-moz-range-progress {
            background: $gold !important;
            background: black;
            border-radius: 0px;
            height: 1px;
        }
    }

    #{$parent}__control {
        border-radius: 0;
        background-color: transparent !important;

        @include tablet {
            background-color: #1B2449 !important;
            z-index: 2;
        }

        &#{$parent}__control-play {
            background: url(../img/icon-play.svg) no-repeat center center;
            transition: 0s;


            &.plyr__control--pressed {
                background: url(../img/icon-pause.svg) no-repeat center center;
            }
        }

        &#{$parent}__control-sound {
            background: url(../img/icon-sound-on.svg) no-repeat center center;
            transition: 0s;


            &.plyr__control--pressed {
                background: url(../img/icon-mute.svg) no-repeat center center;
            }
        }

        &-button {
            height: 100%;
            border-right: 1px solid #fff;
            width: 75px;
            @apply flex justify-center items-center;
            z-index: 2;
        }
    }

    &__progress {
        width: 100%;
        height: 100%;
        left: -1px;
        margin: 0;

        @include tablet {
            position: absolute;
            left: 50%;
            width: 1440px;
        }

        &__buffer {
            display: none;
        }
    }
}